<template>
  <div class="hello">
    <img class="extension-img" src="../assets/img/extensao.jpg" alt="Instale a extensão" @click="installExtension">
    <div class="flex-column menu">
      <router-link to="/">
        <el-card>
          Página Inicial
          <p class="info">Destaques</p>
        </el-card>
      </router-link>
      <router-link to="/materias_alunos">
        <el-card>
          Matérias por Aluno
          <p class="info">Gráfico com a quantidade de matérias requisitadas por aluno.</p>
        </el-card>
      </router-link>
      <router-link to="/chutes_inevitaveis">
        <el-card>
          Chutes Inevitáveis por Turno
          <p class="info">Quantidade total de requisições indeferidas que não podem ser evitadas nem com realocações, mas apenas com abertura de novas turmas ou aumento do número de vagas por turma.</p>
        </el-card>
      </router-link>
      <router-link to="/demanda_geral">
        <el-card>
          Demanda Geral e por Turno
          <p class="info">Quantidade total de vagas, requisições, e a diferença entre vagas e requisições. Em toda a Universidade.</p>
        </el-card>
      </router-link>
      <router-link to="/demanda_curso">
        <el-card>
          Demanda por Curso
          <p class="info">Quantidade total de vagas, requisições, e a diferença entre vagas e requisições. Por Curso.</p>
        </el-card>
      </router-link>
      <router-link to="/info_all_cursos">
        <el-card>
          Demanda por Disciplinas de um Curso
          <p class="info">Quantidade total de vagas, requisições, e a diferença entre vagas e requisições. Por Disciplinas de um Curso.</p>
        </el-card>
      </router-link>
      <router-link to="/previsao">
        <el-card>
          Previsão
          <p class="info">Previsão de Corte de uma Disciplina.</p>
        </el-card>
      </router-link>
      <router-link to="/previsao_curso">
        <el-card>
          Previsão Curso
          <p class="info">Previsão de Corte das Disciplinas de um Curso.</p>
        </el-card>
      </router-link>
    </div>
    <router-view class="view"></router-view>
    <footer><p><small>&copy; 2016-present Charbel Rami, Felipe Santos. <a href="https://github.com/charbelrami/ufabc-enrollment-data">Source code</a> licensed under <a href="https://raw.githubusercontent.com/charbelrami/ufabc-enrollment-data/master/license">the MIT License</a>.</small></p></footer>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    methods: {
      installExtension() {
        chrome.webstore.install()
      }
    }
  }
</script>

<style scoped>
img {
  max-width: 100%;
  height: auto;
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.menu a {
  color: #1F2D3D;
  font-weight: bolder;
  text-decoration: none;
}
.info {
  margin: 0;
  font-size: smaller;
  color: #475669;
  font-weight: normal;
}
.extension-img {
  cursor: pointer;
}
.view {
  min-height: 100vh;
  padding-top: 20px;
}
</style>



